<template>
    <div>
        <hader/>
        <div class="partner">
            <div class ="banner">
                <div class="tit">[ 让每一个商户都成为我们的朋友 ]</div>
                <div class="cont">
                    <span>网址：www.dabojituan.com </span>
                    <span> 邮箱：dbzhpt@gmail.com</span>
                    <div>接待时间：7x24 小时</div>
                </div>
            </div>
            <div class="cooperation"> 
                <div class="tit">合作方式</div>
                <div class="cont">大博集团游戏研发公司平台（网址Www.dabojituan.com；邮箱dbzhpt@gmail.com ），可以提供多样化的合作框架，任何能够共创双赢局面合理的合作方案只要您提出我们都会积极的响应。</div>
                <div class="tab">
                    <div class="tabHead">
                        <div class="programme">方案</div>
                        <div class="content">方案内容</div>
                        <div class="remarks">备注</div>
                    </div>
                    <div class="tabody">
                        <div class="details">
                            <div class="programme">租网</div>
                            <div class="content">大博平台可提供综合包网</div>
                            <div class="remarks">来电商议</div>                            
                        </div>
                        <div class="details">
                            <div class="programme">租网</div>
                            <div class="content">大博平台可提供棋牌包网</div>
                            <div class="remarks">来电商议</div>                            
                        </div>
                        <div class="details">
                            <div class="programme">租网</div>
                            <div class="content">大博平台可提供彩票包网</div>
                            <div class="remarks">来电商议</div>                            
                        </div>
                    </div>
                </div>
                <div class="procedure"> 
                    <div class="tit">申请流程介绍</div>
                    <div class="step">
                        <div class="dtl" v-for="item in list" :key="item.id">
                            <div class="img">
                                <img :src="item.img" alt="">
                            </div>
                            <div>
                                <div class="title">{{item.name}}</div>
                                <div class="cont">{{item.cont}}</div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <footera/>
        <div class="nav row">
            <div :class="{active:nav == 'index'}" @click="navClick('index')">
                <div>
                    <img :src="nav == 'index'?img.indexa:img.index" >
                </div>
                <div>
                    <p>首页</p>
                </div>
            </div>
            <div :class="{active:nav == 'product'}" @click="navClick('product')">
                <div>
                    <img :src="nav == 'product'?img.producta:img.product">
                </div>
                <div>
                    <p>产品</p>
                </div>
            </div>
            <div :class="{active:nav == 'advantage'}" @click="navClick('advantage')"> 
                <div>
                    <img :src="nav == 'advantage'?img.advantagea:img.advantage">
                </div>
                <div>
                    <p>优势</p>
                </div>
            </div>
            <div :class="{active:nav == 'partner'}" @click="navClick('partner')">
                <div>
                    <img :src="nav == 'partner'?img.partnera:img.partner">
                </div>
                <div>
                    <p>伙伴</p>
                </div>
            </div>
            <div :class="{active:nav == 'customized'}" @click="navClick('customized')">
                <div>
                    <img :src="nav == 'customized'?img.creationa:img.creation">
                </div>
                <div>
                    <p>定制</p>
                </div>  
            </div>
            <div :class="{active:nav == 'about'}" @click="navClick('about')">
                <div>   
                    <img :src="nav == 'about'?img.abouta:img.about">
                </div>
                <div>
                    <p>关于</p>
                </div>
            </div>
        </div>        
    </div>
</template>
<script>
import hader from '@/components/header';
import footera from '@/components/footer';
export default {
    name:'partner',
    components:{
        hader,footera
    },
    data(){
        return{
            list:[
                {
                    img:require('@/assets/st-imges/1@2x.png'),
                    name : '第一步',
                    cont : '填写您的合作意向（联系方式合作计划等） 并且发送到dbzhpt@gmail.com'      
                },
               {
                    img:require('@/assets/st-imges/2@2x.png'),
                    name : '第二步',
                    cont : '在72小时之内我们将有客服专员与您进行合作计划的沟通'      
                },
                {
                    img:require('@/assets/st-imges/3@2x.png'),
                    name : '第三步',
                    cont : '您需要在“合营计划表”中填写您信息'      
                },
               {
                    img:require('@/assets/st-imges/4@2x.png'),
                    name : '第四步',
                    cont : '在72小时之内合作伙伴专员将与您进一步洽谈商务计划'      
                },
               {
                    img:require('@/assets/st-imges/5@2x.png'),
                    name : '第五步',
                    cont : '签订合作伙伴合作协议'      
                },                                                   
            ],
            data:'',
            nav:'customized',
            img:{
                index:require('@/assets/st-imges/index.png'),
                indexa:require('@/assets/st-imges/indexa.png'),
                about:require('@/assets/st-imges/about.png'),
                abouta:require('@/assets/st-imges/abouta.png'),
                creation:require('@/assets/st-imges/creation.png'),
                creationa:require('@/assets/st-imges/creationa.png'),
                advantage:require('@/assets/st-imges/advantage.png'),
                advantagea:require('@/assets/st-imges/advantagea.png'),
                partner:require('@/assets/st-imges/partner.png'),
                partnera:require('@/assets/st-imges/partnera.png'),
                product:require('@/assets/st-imges/product.png'),
                producta:require('@/assets/st-imges/producta.png'),
            }  
        }
    },
    methods:{
        navClick(str){
            // this.nav = str
            if(str == this.nav ) return false;
            let path = str;
            if(str == 'index'){
                path ='/'
            }
            this.$router.push({ path: path });
        }
    }    
}
</script>
<style scoped>
    .banner{
        background: url("./../../assets/st-imges/partnerBanner@2x.png") no-repeat;
        background-size: 100% 100%;
        background-color:rgba(20,10,3,1); 
        /* height: 1.3rem; */
        color: #fff;
        padding: 0.54rem 0.5rem 0.64rem;
    }
    .banner .tit{
        text-align: center;
        font-size: 0.36rem;
        margin-bottom: 0.12rem;
    }
    .banner .cont{
        text-align: center;
        font-size: 0.26rem;
        line-height: 0.4rem;
    }    
    .cooperation{
        margin: 0 0.3rem;
        text-align: center;
    }
    .cooperation .tit,.procedure .tit{
        margin: 0.4rem 0 0.48rem;
        position: relative;
        font-size: 0.34rem;
        color: #555555;
        font-weight:bold;
    }
    .cooperation .tit::after,.procedure .tit::after{
        display: inline-block;
        content: '';
        width: 0.6rem;
        height: 0.02rem;
        background: #E63440;
        position: absolute;
        bottom: -0.1rem;
        left: 45%;
    }
    .cooperation .cont{
        color: #666666;
        text-align: left;
        font-size: 0.26rem;
        line-height:0.37rem;    
        margin-bottom: 0.2rem;  
    }
    .tab{
        background:#F5F5F5;
        font-size: 0.26rem;
        color: #FFFFFF;
    }
    .tab .tabHead{
        height: 0.7rem;
        line-height: 0.7rem;

        display: flex;
    }
    .tab .tabody .details{
        display: flex;
        height: 0.7rem;
        line-height: 0.7rem;
    }
    .programme{
        width: 0.84rem;
        text-align: center;
        background: #FFE5DB;
        text-align:center;
    }
    .content{
        width: 4.66rem;
        padding-left: 0.2rem;
        text-align: left;
        background: #FFE5DB;
    }
    .remarks{
        width: 1.36rem;
        text-align: center;
        background: #FFE5DB;
    }

    .tabHead div{
        background: #E63440;
        margin: 0 0.02rem 0.02rem 0 ;
    }
    .tabody .details{
        margin-bottom: 0.02rem ;       
        color: #555555;
        font-size: 0.24rem;
    }
    .tabody .details:nth-last-child(1){
        margin-bottom: 0;
    }    
    .details div{
        margin-right: 0.02rem;
    }
    .details div:nth-last-child(1){
        margin-right: 0;
    }
    .step .dtl{
        height: 2rem;
        box-shadow:0px 0.2rem 0.2rem rgba(0,0,0,0.05);
        background: #fff;
        display: flex;
        /* justify-content: center; */
        /* align-self: center; */
        padding: 0.45rem 0.32rem 0.45rem 0.5rem;
        margin-bottom: 0.3rem;
        position: relative;
    }    
    .step .dtl::after{
        content: '';
        display: inline-block;
        width: 0;
        height: 0;
        border: 0.28rem solid #fff;
		border-bottom-color: transparent;
		border-left-color: transparent;
		border-right-color: transparent; 
        position: absolute;
        bottom: -0.55rem;
        left: 45%;      
    }
    .step .dtl:nth-last-child(1)::after{
      border: none;  
    }        
    .dtl .img{
        width: 1.1rem;
        height: 1.1rem;
        margin-right: 0.46rem
    }
    .dtl .img img{
        width: 100%;
        height: 100%;
    }
    .dtl div{
        text-align: left;
    }
    .dtl div .title{
        font-size: 0.3rem;
    }
    .dtl div .cont{
        width: 4.5rem;
        color: #666666;
    }
    .nav{
        position: fixed;
        bottom: 0;
        left: 0;
        background: #fff;
        height: 1rem;
        width: 100%;
    }
    .nav>div{
        width: 16%;
        height: 100%;
    }
    .nav>div>div{
        display: flex;
        justify-content: center;
        align-items: center;
        flex-wrap: wrap;
        height: 50%;
    }
    .nav>div img{
        width: .34rem;
        height: .34rem;
    }
    .list{
        width: 100%;
        height: 100%;
        position: absolute;
        overflow-x: hidden;
        overflow-y: auto;
    }
    .active{
        color: #E63440;
    }            
</style>


